<template>
  <div class="tables table-container" v-bind:class="{'day-table' : day, 'night-table' : !day}">
    <div class="legend">
      <h5 style="color: lightblue;">2 Hours (EOS)</h5>
      <h5 style="color: #90EE90;">1 Hour (EOS)</h5>
      <h5 style="color: red;">Past EOS</h5>
    </div>
    <div>
      <div class="above-max-table">
        <h4>
          Above Max (
          <span id="above-max-count">{{ aboveMax.length }}</span> )
        </h4>
        <table>
          <thead>
            <th>Unit</th>
            <th>Arrivals</th>
            <th>Task Time</th>
            <th>Post Time</th>
            <th>Drive Time</th>
            <th>On Call Time</th>
            <th>Post Moves</th>
            <th>Status</th>
          </thead>
          <tbody>
            <tr
              :key="unit.unit"
              v-for="unit in aboveMax"
              v-bind:style="[ unit.threshold >= 0.83 && unit.threshold < 0.92 ? {'background' : 'lightblue', 'color' : 'var(--primary)'} : {}, unit.threshold >= 0.92 && unit.threshold < 1 ? {'background' : '#90EE90', 'color' : 'var(--primary)'} : {} ]"
            >
              <td>{{ unit.unit }}</td>
              <td>{{ unit.arrivals }}</td>
              <td>{{ unit.task_time }}</td>
              <td>{{ unit.post_time }}</td>
              <td>{{ unit.drive_time }}</td>
              <td>{{ unit.on_call_time }}</td>
              <td>{{ unit.post_assignments }}</td>
              <td>{{ unit.status }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <!--  -->
      <div class="above-current-table">
        <h4>
          Above Current (
          <span id="above-current-count">{{ aboveCurrent.length }}</span> )
        </h4>
        <table>
          <thead>
            <th>Unit</th>
            <th>Arrivals</th>
            <th>Task Time</th>
            <th>Post Time</th>
            <th>Drive Time</th>
            <th>On Call Time</th>
            <th>Post Moves</th>
            <th>Status</th>
          </thead>
          <tbody>
            <tr
              :key="unit.unit"
              v-for="unit in aboveCurrent"
              v-bind:style="[ unit.threshold >= 0.83 && unit.threshold < 0.92 ? {'background' : 'lightblue', 'color' : 'var(--primary)'} : {}, unit.threshold >= 0.92 && unit.threshold < 1 ? {'background' : '#90EE90', 'color' : 'var(--primary)'} : {} ]"
            >
              <td>{{ unit.unit }}</td>
              <td>{{ unit.arrivals }}</td>
              <td>{{ unit.task_time }}</td>
              <td>{{ unit.post_time }}</td>
              <td>{{ unit.drive_time }}</td>
              <td>{{ unit.on_call_time }}</td>
              <td>{{ unit.post_assignments }}</td>
              <td>{{ unit.status }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <!--  -->
      <div class="other-table">
        <h4>
          Other (
          <span id="other-count">{{ other.length }}</span> )
        </h4>
        <table>
          <thead>
            <th>Unit</th>
            <th>Arrivals</th>
            <th>Task Time</th>
            <th>Post Time</th>
            <th>Drive Time</th>
            <th>On Call Time</th>
            <th>Post Moves</th>
            <th>Status</th>
          </thead>
          <tbody>
            <tr
              :key="unit.unit"
              v-for="unit in other"
              v-bind:style="[ unit.threshold >= 0.83 && unit.threshold < 0.92 ? {'background' : 'lightblue', 'color' : 'var(--primary)'} : {}, unit.threshold >= 0.92 && unit.threshold < 1 ? {'background' : '#90EE90', 'color' : 'var(--primary)'} : {} ]"
            >
              <td>{{ unit.unit }}</td>
              <td>{{ unit.arrivals }}</td>
              <td>{{ unit.task_time }}</td>
              <td>{{ unit.post_time }}</td>
              <td>{{ unit.drive_time }}</td>
              <td>{{ unit.on_call_time }}</td>
              <td>{{ unit.post_assignments }}</td>
              <td>{{ unit.status }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "workloadTables",
  data() {
    return {
      units: [],
      day: true
    };
  },
  computed: {
    fixedTaskAndPostTimes: function() {
      function formatTime(int) {
        let hours = Math.floor(int / 60);
        let minutes = int % 60;

        if (minutes < 10) {
          minutes = "0" + minutes;
        }

        return hours + ":" + minutes;
      }
      return this.units.map(function(unit) {
        unit.post_time = formatTime(unit.post_time);
        unit.drive_time = formatTime(unit.drive_time);
        unit.on_call_time = formatTime(unit.task_time);

        if (unit.arrivals > 0)
          unit.task_time = Math.round(unit.task_time / unit.arrivals);

        return unit;
      });
    },
    aboveMax: function() {
      return this.fixedTaskAndPostTimes.filter(unit => {
        return unit.workload >= 1;
      });
    },
    aboveCurrent: function() {
      return this.fixedTaskAndPostTimes.filter(unit => {
        return unit.workload < 1 && unit.workload > unit.threshold;
      });
    },
    other: function() {
      return this.fixedTaskAndPostTimes.filter(unit => {
        return unit.workload <= unit.threshold;
      });
    }
  },
  methods: {
    watchLiveWorkloadData() {
      this.$store.watch(() => {
        let data = this.$store.getters["GET_LIVE_WORKLOAD"];

        let newArray = [...data];

        newArray.sort((a, b) => {
          return b.workload - a.workload;
        });

        this.units = newArray;
      });
    },
    watchStyleChange() {
      this.$store.watch(() => {
        this.day = JSON.parse(this.$store.getters["GET_STYLE_CHANGE"]);
      });
    }
  },
  created() {
    this.watchLiveWorkloadData();
    this.watchStyleChange();
  }
};
</script>

<style lang="scss" scoped>
.legend {
  display: flex;
  justify-content: space-around;
  padding: 1em;
}

.above-max-table {
  th {
    background: red;
    color: white;
  }
}

.above-current-table {
  th {
    background: yellow;
    color: var(--primary);
  }
}

h4 {
  margin-top: 2em;
}

@media (min-width: 960px) {
  .tables > div:nth-child(2) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 1em;
  }
}
</style>
