import axios from "axios";
import router from "../router";

const LOGIN_USER = ({ commit }, data) => {
  return new Promise((resolve, reject) => {
    axios
      .post("/auth/login", data)
      .then((res) => {
        commit("SET_AUTH_TOKEN", res.data.token);
        commit("SET_CURRENT_USER", res.data.user);
        resolve(true);
      })
      .catch((err) => reject(err));
  });
};

const LOGOUT_USER = ({ commit }) => {
  commit("REMOVE_AUTH_TOKEN");
  router.replace({
    name: "Login",
    params: { success: "You have been logged out!" },
    replace: true,
  });
};

const FETCH_LIVE_WORKLOAD = ({ commit, getters }) => {
  let config = {
    headers: {
      authorization: getters.GET_TOKEN,
    },
  };
  axios
    .post("/api/live_workload", null, config)
    .then((res) => {
      commit("NEW_LIVE_WORKLOAD_DATA", res.data);
      commit("LAST_UPDATED_AT", res.data);
    })
    .catch(() => {
      router.replace({
        name: "Login",
        params: { error: "User logged out!" },
        replace: true,
      });
      localStorage.removeItem("auth_token");
    });
};

const CHANGE_STYLE = ({ commit }) => commit("CHANGE_STYLE");

const FETCH_SYSTEM = ({ commit, getters }) => {
  let config = {
    headers: {
      authorization: getters.GET_TOKEN,
    },
  };
  axios
    .post("/api/system", null, config)
    .then((res) => {
      commit("NEW_SYSTEM_DATA", res.data);
    })
    .catch(() => {
      router.replace({
        name: "Login",
        params: { error: "User logged out!" },
        replace: true,
      });
      localStorage.removeItem("auth_token");
    });
};

export default {
  LOGIN_USER,
  LOGOUT_USER,
  FETCH_LIVE_WORKLOAD,
  CHANGE_STYLE,
  FETCH_SYSTEM,
};
