const GET_TOKEN = (state) => `Bearer ${state.auth_token}`;

const GET_LIVE_WORKLOAD = (state) => state.liveWorkloadData;

const GET_IF_CURRENT_DATA = (state) => {
  if (state.LAST_UPDATED_AT !== null) return timeDifference(state.LAST_UPDATED_AT);
};

const GET_LAST_UPDATED_TIME = (state) => state.LAST_UPDATED_AT;

const GET_USER = (state) => state.CURRENT_USER;

const GET_STYLE_CHANGE = (state) => state.day;

const GET_SYSTEM_LOGS = (state) => state.systemData;

const GET_BAR_CHART_DATA = (state) => {
  let data = state.liveWorkloadData;

  let filter = new FilterData();

  filter.filterData(data);

  return filter.dataObject;
};

class FilterData {
  constructor() {
    this.dataObject = {
      labels: [],
      datasets: [
        {
          label: "Current Threshold",
          data: [],
          backgroundColor: [],
          borderWidth: 1,
        },
        {
          label: "Current Workload",
          data: [],
          backgroundColor: [],
          borderWidth: 1,
        },
        {
          label: "Max Threshold(1.0) Beta",
          fill: false,
          borderColor: "blue",
          backgroundColor: "blue",
          data: [],
          radius: 0,

          // Changes this dataset to become a line
          type: "line",
        },
        {
          label: "Above Current Threshold",
          backgroundColor: "rgba(255, 255, 0, 0.75)",
        },
        {
          label: "Above Max Threshold",
          backgroundColor: "rgba(255, 0, 0, 0.5)",
        },
      ],
    };
  }

  filterData(data) {
    data.forEach((unit) => {
      this.dataObject.labels.push(unit.unit);

      if (unit.workload >= 1) {
        this.dataObject.datasets[1].backgroundColor.push(
          "rgba(255, 0, 0, 0.5)"
        );
        this.dataObject.datasets[0].backgroundColor.push("#2E4E64");
      } else if (unit.workload > unit.threshold) {
        this.dataObject.datasets[1].backgroundColor.push(
          "rgba(255, 255, 0, 0.75)"
        );
        this.dataObject.datasets[0].backgroundColor.push("#2E4E64");
      } else {
        this.dataObject.datasets[1].backgroundColor.push("#32A9D9");
        this.dataObject.datasets[0].backgroundColor.push(
          "rgba(46, 78, 100, 0.8)"
        );
      }

      if (unit.workload < 0) {
        unit.workload = 0;
      }

      this.dataObject.datasets[0].data.push(unit.threshold);

      this.dataObject.datasets[1].data.push(unit.workload);

      this.dataObject.datasets[2].data.push(1);
    });
  }
}

function timeDifference(time) {
  let bool;

  const [newTime, modifier] = new Date().toLocaleTimeString().split(" ");

  let [hours, minutes] = newTime.split(":");

  if (hours === "12") {
    hours = "00";
  }

  if (modifier === "PM") {
    hours = parseInt(hours, 10) + 12;
  }

  let time_start = new Date();
  let time_end = new Date();

  const value_start = time.split(":");
  const value_end = `${hours}:${minutes}`.split(":");

  time_start.setHours(value_start[0], value_start[1], 0);
  time_end.setHours(value_end[0], value_end[1], 0);

  let diff = Math.abs((time_end - time_start) / 1000);

  // 5 minutes
  if (diff >= 300) {
    bool = false;
  } else {
    bool = true;
  }

  return bool;
}

export default {
  GET_TOKEN,
  GET_LIVE_WORKLOAD,
  GET_IF_CURRENT_DATA,
  GET_LAST_UPDATED_TIME,
  GET_USER,
  GET_STYLE_CHANGE,
  GET_SYSTEM_LOGS,
  GET_BAR_CHART_DATA,
};
