<template>
  <div class="log table-container" v-bind:class="{'day-table' : day, 'night-table' : !day}">
    <h4>System Logs ( {{ logs.length }} )</h4>
    <table>
      <thead>
        <th>Log</th>
        <th>Driving</th>
        <th>Posting</th>
        <th>Level</th>
      </thead>
      <tbody>
        <tr v-for="log in logs" :key="log.log">
          <td>{{ log.log }}</td>
          <td>{{ log.driving }}</td>
          <td>{{ log.posting }}</td>
          <td>{{ log.level }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "Log",
  data() {
    return {
      logs: [],
      day: true
    };
  },
  computed: {
    getLogs: function() {
      return this.logs.map(log => {
        return log;
      });
    }
  },
  methods: {
    watchSystemLogs() {
      this.$store.watch(() => {
        let data = this.$store.getters["GET_SYSTEM_LOGS"];
        this.logs = data[0].logs;
      });
    },
    watchStyleChange() {
      this.$store.watch(() => {
        this.day = JSON.parse(this.$store.getters["GET_STYLE_CHANGE"]);
      });
    }
  },
  created() {
    this.watchStyleChange();

    setTimeout(() => {
      this.watchSystemLogs();
    }, 1000);
  }
};
</script>

<style lang="scss" scoped>
td:nth-child(1) {
  text-align: left;
  padding-left: 5px;
}

.log{
  max-width: 1080px;
  margin: 0 auto 3em auto;
}
</style>