<template>
  <div class="dashboard">
    <Cards />
    <workloadChartContainer />
    <hr />
    <workloadTables />
    <hr />
    <crewTable />
    <hr />
    <logTable />

    <outdatedPopup v-if="!current" />
  </div>
</template>

<script>
import outdatedPopup from "../components/dashboard/outdatedPopup";
import Cards from "../components/dashboard/liveWorkload/cards";
import workloadTables from "../components/dashboard/liveWorkload/workloadTables";
import crewTable from "../components/dashboard/liveWorkload/crewTable";
import logTable from "../components/dashboard/system/logTable";
import workloadChartContainer from "../components/dashboard/liveWorkload/workloadChartContainer";

export default {
  name: "Dashboard",
  components: {
    outdatedPopup,
    Cards,
    workloadChartContainer,
    workloadTables,
    crewTable,
    logTable,
  },
  data() {
    return {
      current: true,
    };
  },
  methods: {
    loadBar() {
      const resetNum = 600;
      let num = 0;
      this.fetchingLiveWorkload = setInterval(() => {
        document.querySelector("#load-bar").style.width = `${(num / resetNum) *
          100}%`;
        num++;
        if (num === resetNum) {
          this.$store.dispatch("FETCH_LIVE_WORKLOAD");
          this.$store.dispatch("FETCH_SYSTEM");
          this.current = this.$store.getters["GET_IF_CURRENT_DATA"];
          num = 0;
        }
      }, 100);
    },
  },
  beforeDestroy() {
    clearInterval(this.fetchingLiveWorkload);
  },
  mounted() {
    this.$store.dispatch("FETCH_LIVE_WORKLOAD");
    this.$store.dispatch("FETCH_SYSTEM");
    setTimeout(() => {
      this.current = this.$store.getters["GET_IF_CURRENT_DATA"];
    }, 1000);
    this.loadBar();
  },
};
</script>

<style lang="scss" scoped>
hr {
  border-color: rgb(29, 29, 141);
}

.dashboard {
  position: relative;
}
</style>
