<template>
  <div class="login" v-bind:class="{ day: day, night: !day }">
    <h3>Login</h3>
    <form @submit.prevent="loginUser">
      <input type="text" v-model="username" placeholder="Username" />
      <input type="password" v-model="password" placeholder="Password" />
      <input
        type="submit"
        value="Submit"
        v-bind:class="{ day: day, night: !day }"
      />
      <p class="error" v-if="error">{{ error }}</p>
      <p class="success" v-if="success">{{ success }}</p>
    </form>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      error: "",
      success: "",
      username: "",
      password: "",
      day: true,
    };
  },
  created() {
    const error = this.$route.params.error;
    if (error) this.error = error;

    const success = this.$route.params.success;
    if (success) this.success = success;

    setTimeout(() => {
      this.error = "";
      this.success = "";
    }, 5000);

    this.day = JSON.parse(this.$store.getters["GET_STYLE_CHANGE"]);
  },
  methods: {
    loginUser() {
      this.$store
        .dispatch("LOGIN_USER", {
          username: this.username,
          password: this.password,
        })
        .then(() => {
          if (
            this.$store.state.auth_token !== null &&
            this.$store.state.auth_token !== undefined
          ) {
            this.$router.replace({ name: "Dashboard" });
          }
        })
        .catch((err) => {
          this.error = err.response.data;
        });
    },
  },
};
</script>

