  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  padding: 1em;
  max-width: 500px;
  margin: auto;
}

form {
  display: grid;
  margin-top: 1em;
  grid-gap: 1em;
  width: 100%;
}

input[type="text"],
input[type="password"] {
  font-size: 1.2rem;
  padding: 0.25em 0.5em;
  border: none;
  background: none;
  border-bottom: solid 1px var(--primary);
}

input[type="submit"] {
  font-size: 1.2rem;
  border: none;
  padding: 0.25em;
  border-radius: 5px;
  transition: 0.4s ease-out;
  width: 50%;
  margin: auto;
}

input[type="submit"]:hover {
  opacity: 0.5;
  transform: translateY(1px);
}

.error {
  padding: 1em;
  background: red;
  color: white;
  border-radius: 5px;
  text-align: center;
}

.success {
  padding: 1em;
  background: lightgreen;
  color: black;
  border-radius: 5px;
  text-align: center;
}

.night {
  h3 {
    color: white;
  }

  input[type="text"],
  input[type="password"] {
    color: var(--primary);
    background: white;
  }

  input[type="submit"] {
    color: var(--primary);
    background: white;
  }
}

.day {
  h3 {
    color: var(--primary);
  }

  input[type="text"],
  input[type="password"] {
    background: white;
  }

  input[type="submit"] {
    background: var(--primary);
    color: white;
  }
}
</style>
