<template>
  <div class="crew-list table-container" v-bind:class="{'day-table' : day, 'night-table' : !day}">
    <h4>Crew List ( {{ units.length }} )</h4>
    <table>
      <thead>
        <th>Unit</th>
        <th>SOS</th>
        <th>Crew Member One</th>
        <th>Crew Member Two</th>
      </thead>
      <tbody>
        <tr v-for="unit in units" :key="unit.unit">
          <td>{{ unit.unit }}</td>
          <td>{{ unit.sos }}</td>
          <td>{{ unit.crew_member_one }}</td>
          <td>{{ unit.crew_member_two }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "crewList",
  data() {
    return {
      units: [],
      day: true
    };
  },
  methods: {
    watchLiveWorkloadData() {
      this.$store.watch(() => {
        let data = this.$store.getters["GET_LIVE_WORKLOAD"];

        let newArray = [...data];

        newArray.sort((a, b) => {
          return a.unit - b.unit;
        });

        this.units = newArray;
      });
    },
    watchStyleChange() {
      this.$store.watch(() => {
        this.day = JSON.parse(this.$store.getters["GET_STYLE_CHANGE"]);
      });
    }
  },
  created() {
    this.watchLiveWorkloadData();
    this.watchStyleChange();
  }
};
</script>

<style lang="scss" scoped>
.crew-list {
  max-width: 1080px;
  margin: auto;
}
</style>