const SET_AUTH_TOKEN = (state, token) => {
  state.auth_token = token;
  localStorage.setItem("auth_token", token);
};

const REMOVE_AUTH_TOKEN = (state) => {
  state.auth_token = null;
  localStorage.removeItem("auth_token");
};

const NEW_LIVE_WORKLOAD_DATA = (state, data) => (state.liveWorkloadData = data);

const SET_CURRENT_USER = (state, data) => {
  state.CURRENT_USER = data;
  localStorage.setItem("current_user", JSON.stringify(data));
};

const CHANGE_STYLE = (state) => {
  state.day = !state.day;
  localStorage.setItem("changeStyle", state.day);
};

const NEW_SYSTEM_DATA = (state, data) => (state.systemData = data);

const LAST_UPDATED_AT = (state, data) => state.LAST_UPDATED_AT = data[0].updated_at;

export default {
  SET_AUTH_TOKEN,
  REMOVE_AUTH_TOKEN,
  NEW_LIVE_WORKLOAD_DATA,
  SET_CURRENT_USER,
  CHANGE_STYLE,
  NEW_SYSTEM_DATA,
  LAST_UPDATED_AT
};
