<template>
  <div class="workload-chart-container">
    <workloadChart v-if="loaded" :chartData="data" :options="options" />
  </div>
</template>

<script>
import workloadChart from "../liveWorkload/workloadChart";

export default {
  name: "workloadChartContainer",
  components: {
    workloadChart,
  },
  data() {
    return {
      loaded: true,
      options: null,
      data: null
    };
  },
  methods: {
    chartDesign() {
      let options = {
        maintainAspectRatio: false,
        animation: false,
        responsive: true,
        scales: {
          xAxes: [
            {
              stacked: true,
            },
          ],
        },
      };

      this.options = options;
    },
    watchChartData() {
      this.$store.watch(() => {
        this.data = this.$store.getters["GET_BAR_CHART_DATA"];
      });
    },
  },
  created() {
    this.chartDesign();
    this.watchChartData();
  },
};
</script>

<style lang="scss" scoped>
.workload-chart-container {
  height: 500px;
  // padding: 1em;
  max-width: 1080px;
  margin: auto;
  box-sizing: border-box;
}
</style>
