<template>
  <div class="nav" v-if="this.$route.name !== 'Login'">
    <span v-if="user.name !== ''">Current User: {{ user.name }}</span>
    <button id="changeStyle" v-on:click="changeStyle">
      <i v-bind:class="{ 'fas fa-sun': !day, 'fas fa-moon': day }"></i>
    </button>
    <button v-on:click="logoutUser">LOGOUT</button>
    <button class="exit-btn" v-on:click="closeNav">X</button>
    <button class="menu-btn" v-on:click="openNav">MENU</button>
  </div>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      user: {},
      day: true
    };
  },
  methods: {
    logoutUser() {
      this.$store.dispatch("LOGOUT_USER");
    },
    getUser() {
      setTimeout(() => {
        this.user = JSON.parse(this.$store.getters["GET_USER"]);
      }, 1000);
    },
    changeStyle() {
      this.$store.dispatch("CHANGE_STYLE");
      this.day = this.$store.getters["GET_STYLE_CHANGE"];
    },
    closeNav() {
      document.querySelector(".nav").style.right = "-200px";
      document.querySelector(".menu-btn").style.opacity = 1;
    },
    openNav() {
      document.querySelector(".nav").style.right = "0";
      document.querySelector(".menu-btn").style.opacity = 0;
    }
  },
  created() {
    this.day = JSON.parse(this.$store.getters["GET_STYLE_CHANGE"]);
    this.getUser();

    document.body.addEventListener("click", e => {
      let element = e.target.parentNode;

      if (element.className !== "nav") {
        this.closeNav();
      }
    });
  }
};
</script>

<style lang="scss" scoped>
.nav {
  position: absolute;
  width: 200px;
  right: -200px;
  display: grid;
  background: var(--primary);
  z-index: 1000;

  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);

  span {
    font-size: 0.8rem;
    padding: 1em 1em;
    border-bottom: solid white 1px;
  }

  .exit-btn {
    background: rgb(165, 7, 7);
  }

  .menu-btn {
    position: absolute;
    bottom: 50%;
    left: -65px;
    border: none;
    background: rgba(165, 7, 7, 0.75);
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
    z-index: 1000;
  }

  button {
    background: var(--primary);
    border: none;
    color: white;
    padding: 1em;
    transition: 0.4s ease;
    cursor: pointer;
    border-bottom: solid white 1px;

    &:hover {
      opacity: 0.25;
      background: white;
      color: var(--primary);
    }
  }

  #changeStyle {
    padding: 1em 2em;
  }
}
</style>
