<template>
  <div class="cards">
    <div
      v-bind:class="[
        { 'day-cards': day && level > 0, 'night-cards': !day && level > 0 },
        { warning: level === 0 },
      ]"
      class="card"
    >
      <div><h2>Level</h2></div>
      <div>
        <h2>{{ level }}</h2>
      </div>
    </div>
    <div v-bind:class="{ 'day-cards': day, 'night-cards': !day }" class="card">
      <div><h2>On Call</h2></div>
      <div>
        <h2>{{ on_call }}</h2>
      </div>
    </div>
    <div v-bind:class="{ 'day-cards': day, 'night-cards': !day }" class="card">
      <div><h2>Posting</h2></div>
      <div>
        <h2>{{ posting }}</h2>
      </div>
    </div>
    <div v-bind:class="{ 'day-cards': day, 'night-cards': !day }" class="card">
      <div><h2>Driving</h2></div>
      <div>
        <h2>{{ driving }}</h2>
      </div>
    </div>
    <div v-bind:class="{ 'day-cards': day, 'night-cards': !day }" class="card">
      <div><h2>Fueling</h2></div>
      <div>
        <h2>{{ fueling }}</h2>
      </div>
    </div>
    <div v-bind:class="{ 'day-cards': day, 'night-cards': !day }" class="card">
      <div><h2>EOS</h2></div>
      <div>
        <h2>{{ eos }}</h2>
      </div>
    </div>
    <div
      v-bind:class="[
        { 'day-cards': day && late_call === 0, 'night-cards': !day && late_call === 0 },
        { warning: late_call > 0 },
      ]"
      class="card"
    >
      <div><h2>Late Call</h2></div>
      <div>
        <h2>{{ late_call }}</h2>
      </div>
    </div>
    <div
      v-bind:class="[
        { 'day-cards': day && past_eos === 0, 'night-cards': !day && past_eos === 0 },
        { warning: past_eos > 0 },
      ]"
      class="card"
    >
      <div><h2>Past EOS</h2></div>
      <div>
        <h2>{{ past_eos }}</h2>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Cards",
  data() {
    return {
      level: 0,
      on_call: 0,
      posting: 0,
      driving: 0,
      fueling: 0,
      eos: 0,
      late_call: 0,
      past_eos: 0,
      day: true,
    };
  },
  methods: {
    watchLiveWorkloadData() {
      this.$store.watch(
        () => {
          return this.$store.getters["GET_LIVE_WORKLOAD"];
        },
        (newData) => {
          let on_call = 0;
          let driving = 0;
          let posting = 0;
          let fueling = 0;
          let eos = 0;
          let late_call = 0;
          let past_eos = 0;
          let sos = 0;

          newData.forEach((unit) => {
            if (unit.status === "On Call") on_call++;
            else if (unit.status === "Posting") posting++;
            else if (unit.status === "Driving") driving++;
            else if (unit.status === "Fueling") fueling++;
            else if (unit.status === "EOS") eos++;
            else if (unit.status === "Late Call") late_call++;
            else if (unit.status === "Past EOS") past_eos++;
            else if (unit.status === "SOS") sos++;
          });

          this.level = driving + posting + sos;
          this.on_call = on_call;
          this.driving = driving;
          this.posting = posting;
          this.fueling = fueling;
          this.eos = eos;
          this.late_call = late_call;
          this.past_eos = past_eos;
        }
      );
    },
    watchStyleChange() {
      this.$store.watch(() => {
        this.day = JSON.parse(this.$store.getters["GET_STYLE_CHANGE"]);
      });
    },
  },
  created() {
    this.watchLiveWorkloadData();
    this.watchStyleChange();
  },
};
</script>

<style lang="scss" scoped>
.cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1em;
  margin-top: 3em;
  z-index: -100;
  padding: 1em;
}

.card {
  width: 100%;
  text-align: center;
  box-shadow: 0 0 10px rgba(1, 1, 1, 0.5);
  border-radius: 5px;
  z-index: -100;
}

h2 {
  font-size: 0.9rem;
}

.card > div:nth-child(1) {
  padding: 0.5em 0 0 0;
}

.card > div:nth-child(2) {
  position: relative;
  height: 50px;

  h2 {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.warning {
  background: red;
  color: white;

  & > div:nth-child(1) {
    border-bottom: solid 1px white;
  }
}

.day-cards {
  color: var(--primary);
  background: white;

  & > div:nth-child(1) {
    border-bottom: solid 1px var(--primary);
  }
}

.night-cards {
  color: white;
  background: var(--primary);

  & > div:nth-child(1) {
    border-bottom: solid 1px white;
  }
}

@media (min-width: 860px) {
  .cards {
    grid-template-columns: repeat(8, 1fr);
  }
}
</style>
